 .third {
    background-color: #fda34b;
    margin-bottom: -80px;
  }
  
  h3 {
    font-size: 3rem;
  }
  
  .cards-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
   
  .card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 200px;
    width: 160px;
    background-color: white;
    margin: 10px;
    box-shadow: 0 1px rgba(0, 0, 0, 12), 0 1px 2px rgba(0, 0, 0, 12);
    transition: all 0.3s cubic-bezier(025, 0.8, 0.25, 1);
  }
  
  .card:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 25), 0 10px 10px rgba(0, 0, 0, 25);
  }
  
  .img-wrapper {
    width: 110px;
    height: 90px;
    text-align: center;
    padding: 5px;
  }
  
  img {
    max-width: 100%;
    max-height: 500%;
    align-items: center;
    padding-left: 70px;
  }
  
  .skill-title-wrapper {
    padding: 5px;
    padding-right: 60px;
    height: 100px;
    width: 110px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: flex-end;
  }
  